<template>
  <div class="WebSerialApi">
    <el-button @click="get_one">自动选择串口</el-button>
    <el-button @click="get_open">open</el-button>
    <el-button @click="get_close">close</el-button>
    <el-button @click="get_new">重新连接</el-button>
  </div>
</template>

<script>
export default {
  name: 'WebSerialApi',
  components: {
  },
  data () {
    return {
      ws: ''
    }
  },
  mounted () {
    this.webSocket()
  },
  methods: {
    // 发送websocket连接
    webSocket () {
      if ('WebSocket' in window) {
        let that = this
        that.ws = new WebSocket('ws://127.0.0.1:8003')
        console.log(that.ws)
        that.ws.onopen = function () {
          console.log('open')
          // var message = JSON.stringify(this.data)
          that.ws.send('连接脚本')
        }
        that.ws.onmessage = function (e) {
          console.log(e)
          if (e.data === '连接脚本成功') {
            that.$message({
              type: 'success',
              message: '连接脚本成功!'
            })
          } else if (e.data === '自动选择正确的串口成功') {
            that.$message({
              type: 'success',
              message: '自动选择正确的串口成功!'
            })
          } else if (e.data === '没有连接正确的串口') {
            that.$message({
              type: 'error',
              message: '自动选择正确的串口失败!'
            })
          } else if (e.data === '开门成功') {
            that.$message({
              type: 'success',
              message: '开门成功!'
            })
          } else if (e.data === '开门失败') {
            that.$message({
              type: 'success',
              message: '开门失败!'
            })
          } else if (e.data === '关门成功') {
            that.$message({
              type: 'success',
              message: '关门成功!'
            })
          } else if (e.data === '关门失败') {
            that.$message({
              type: 'success',
              message: '关门失败!'
            })
          }
        }
        that.ws.onerror = function (e) {
          that.$message({
            type: 'error',
            message: '串口连接失败！'
          })
        }
        that.ws.onclose = function (e) {
          console.log(e)
          that.get_new()
        }
      } else {
        console.log('您的浏览器不支持WebSocket')
      }
    },
    get_new () {
      this.webSocket()
    },
    get_one () {
      let that = this
      that.timer = setInterval(() => {
        that.ws.send('自动选择串口')
      }, 1000 * 5)
    },
    get_open () {
      let that = this
      that.ws.send('开门')
    },
    get_close () {
      let that = this
      that.ws.send('关门')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



