<template>
  <div class="CompoundGlue">
    <div style="margin:0 auto;border: 1px solid black;width: 85%">
      <h2>创建胶水</h2>
      <h4>当前时间：{{time}}</h4>
      <el-input placeholder="请输入胶水ID" type="textarea" style="width:35%" :autosize="{ minRows: 1, maxRows: 5}" v-model="addGlueId"></el-input>
      <el-button style="background-color: #409eff; color: #fff;width:80px" @click="post_glue">创建</el-button>
    </div>
    <el-dialog
    title="提示"
    :visible.sync="dialogVisible"
    width="30%">
    <h2>创建失败的胶水有：</h2>
    <h3>{{text}}</h3>
    </el-dialog>
    <br>
    <div style="margin:0 auto;border: 1px solid black;width: 85%">
      <h2>查询单个胶水信息</h2>
      <el-input placeholder="请输入胶水ID" style="width:35%" v-model="glueId"></el-input>
      <el-button style="background-color: #409eff; color: #fff;width:80px" icon="el-icon-search" @click="get_glue"></el-button>
      <div v-if="glueVisible" style="margin:0 auto;">
        <br>
        <el-table
          :data="gluedata"
          style="width: 80%;margin:0 auto; border: 1px solid black">
          <el-table-column
            prop="Id"
            label="Id">
          </el-table-column>
          <el-table-column
            prop="GlueId"
            label="GlueId">
          </el-table-column>
          <el-table-column
            prop="CreatedTime"
            label="CreatedTime">
          </el-table-column>
        </el-table>
      </div>
    </div>
    <br>
    <div style="margin:0 auto;border: 1px solid black;width: 85%">
      <h2>查询时间段内的所有胶水信息</h2>
      <el-date-picker
      v-model="value2"
      type="datetimerange"
      :picker-options="pickerOptions"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
    <el-button slot="append" icon="el-icon-search" style="background-color: #409eff; color: #fff; height: 40px; width:80px;" @click="get_period_of_time"></el-button>
    <div v-if="periodTimeVisible" style="margin:0 auto;">
        <br>
        <el-table
          ref="multipleTable"
          :data="periodTimeData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
          :row-class-name="tableRowClassName"
          border
          :row-style="{height: '10px'}"
          :cell-style="{padding:0}"
          style="width: 90%;margin:0 auto; border: 1px solid black">
          <el-table-column
            prop="Id"
            label="Id">
          </el-table-column>
          <el-table-column
            prop="GlueId"
            label="胶水ID">
          </el-table-column>
          <el-table-column
            prop="CreatedTime"
            label="创建时间">
          </el-table-column>
          <el-table-column
            prop="FailureTime"
            label="失效时间"
            sortable>
          </el-table-column>
          <el-table-column
            prop="RecycleFlag"
            label="状态">
          </el-table-column>
          <el-table-column
            prop="RecycleTime"
            label="回收时间"
            sortable>
          </el-table-column>
          <el-table-column
          fixed="right"
            label="操作"
            width="40">
            <template slot-scope="scope">
            <i
              @click="deleteRow(scope.$index, periodTimeData)"
              class="el-icon-s-order"
              style="color:black;background-color: white">
            </i>
          </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            align='center'
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[100, 200, 500, 1000]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="periodTimeData.length">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CompoundGlue',
  components: {
  },
  data () {
    return {
      currentPage: 1,
      pageSize: 100,
      time: '',
      text: '',
      glueId: '',
      addGlueId: '',
      gluedata: [],
      errlist: [],
      postdata: [],
      glueVisible: false,
      dialogVisible: false,
      periodTimeVisible: false,
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            start.setHours(0)
            start.setMinutes(0)
            start.setSeconds(0)
            end.setTime(start.getTime() + 3600 * 1000 * 24 * 7)
            end.setHours(0)
            end.setMinutes(0)
            end.setSeconds(0)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            start.setHours(0)
            start.setMinutes(0)
            start.setSeconds(0)
            end.setTime(start.getTime() + 3600 * 1000 * 24 * 30)
            end.setHours(0)
            end.setMinutes(0)
            end.setSeconds(0)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            start.setHours(0)
            start.setMinutes(0)
            start.setSeconds(0)
            end.setTime(start.getTime() + 3600 * 1000 * 24 * 90)
            end.setHours(0)
            end.setMinutes(0)
            end.setSeconds(0)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      value1: [],
      value2: [],
      startTime: '',
      endTime: '',
      periodTimeData: []
    }
  },
  mounted () {
    this.time = this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
    this.get_time()
    this.get_timer()
  },
  methods: {
    // 每页多少条
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pageSize = val
    },
    // 当前页
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.currentPage = val
    },
    // 打开页面赋值今天的时间段
    get_timer () {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime())
      start.setHours(8)
      start.setMinutes(0)
      start.setSeconds(0)
      end.setTime(start.getTime() + 3600 * 1000 * 24 * 1)
      end.setHours(8)
      end.setMinutes(0)
      end.setSeconds(0)
      this.value2 = [start, end]
      console.log(this.value2)
    },
    // 删除缓存中的数据
    del_local_data () {
      localStorage.removeItem('RemovedData')
    },
    // 搜索时间段
    get_period_of_time () {
      this.periodTimeVisible = true
      this.startTime = this.moment(this.value2[0]).format('YYYY-MM-DD HH:mm:ss')
      this.endTime = this.moment(this.value2[1]).format('YYYY-MM-DD HH:mm:ss')
      console.log(this.startTime)
      console.log(this.endTime)
      this.del_local_data()
      clearInterval(this.datatimer)
      this.get_period_time_data()
      // 定时器 10分钟搜索一次新数据
      this.datatimer = setInterval(() => {
        this.get_period_time_data()
      }, 1000 * 20)
    },
    // 跨域获取时间段内的数据
    get_period_time_data () {
      let that = this
      that.$axios({
        url: '/display/glue/glue-info-by-time-field',
        method: 'get',
        params: {
          startTime: that.startTime,
          endTime: that.endTime
        }
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res !== null && res.length !== 0) {
          for (let i = 0; i < res.length; i++) {
            var ontime = new Date(res[i]['CreatedTime']).setSeconds(new Date(res[i]['CreatedTime']).getSeconds() + 4800)
            res[i]['FailureTime'] = that.moment(ontime).format('YYYY-MM-DD HH:mm:ss')
            if (res[i]['RecycleFlag'] === 0) {
              res[i]['RecycleFlag'] = '未回收'
            } else {
              res[i]['RecycleFlag'] = '已回收'
            }
            // if (ontime <= new Date()) {
            //   res.splice(i, 1)
            //   i--
            // }
          }
          var localdata = localStorage.getItem('RemovedData')
          console.log(localdata)
          if (localdata !== null) {
            var locallist = localdata.split(',')
            console.log(locallist)
            for (let k = 0; k < locallist.length; k++) {
              for (let j = 0; j < res.length; j++) {
                // console.log(locallist[k])
                // console.log(typeof (locallist[k]))
                // console.log(res[j]['Id'])
                if (locallist[k] === String(res[j]['Id'])) {
                  res.splice(j, 1)
                  j = j - 1
                }
              }
            }
          }
          that.periodTimeData = res
          console.log(res)
        }
      })
    },
    // 判断是否在有效时间内
    tableRowClassName ({row, rowIndex}) {
      // return 'success-row'
      if (new Date(row['FailureTime']) <= new Date()) {
        if (row['RecycleFlag'] === '未回收') {
          return 'error-row'
        } else {
          return 'warning-row'
        }
      } else {
        if (row['RecycleFlag'] === '未回收') {
          return 'success-row'
        }
      }
    },
    // 回收按钮
    deleteRow (index, rows) {
      console.log(this.$refs.multipleTable.tableData)
      console.log(this.currentPage)
      console.log(index)
      console.log(rows)
      if (this.$refs.multipleTable.tableData[index]['RecycleFlag'] === '已回收') {
        this.$message({
          type: 'error',
          message: '已回收，无法再次回收！'
        })
      } else {
        // console.log(rows[index]['Id'])
        this.get_delect_data(this.$refs.multipleTable.tableData[index]['GlueId'])
      }
      // console.log(rows.slice(((this.currentPage - 1) * this.pageSize + index), (this.currentPage - 1) * this.pageSize + index + 1)[0]['Id'])
      // console.log(localStorage.getItem('RemovedData'))
      // if (localStorage.getItem('RemovedData') !== null) {
      //   var local = localStorage.getItem('RemovedData')
      //   local = local + ',' + rows.slice(((this.currentPage - 1) * 15 + index), (this.currentPage - 1) * 15 + index + 1)[0]['Id']
      //   localStorage.setItem('RemovedData', local)
      // } else {
      //   localStorage.setItem('RemovedData', rows.slice(((this.currentPage - 1) * 15 + index), (this.currentPage - 1) * 15 + index + 1)[0]['Id'])
      // }
      // this.periodTimeData = rows.slice(0, (this.currentPage - 1) * 15 + index).concat(rows.slice((this.currentPage - 1) * 15 + index + 1))
      // console.log(rows.slice(0, index))
      // console.log(rows.slice(index + 1))
    },
    get_delect_data (id) {
      let that = this
      console.log(id)
      let formdata = new FormData()
      formdata.append('glue_id', id)
      formdata.append('recycle_time', that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss'))
      that.$axios({
        method: 'put',
        url: '/display/glue/recycle-flag-glue-info',
        data: formdata
      }).then(function (response) {
        console.log(response)
        that.get_period_time_data()
      })
    },
    // 定时器
    // 获取当前时间
    get_time () {
      this.timer = setInterval(() => {
        this.time = this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
      }, 1000)
    },
    // 获取胶水信息
    get_glue () {
      console.log(this.glueId)
      let that = this
      that.gluedata = []
      that.$axios({
        url: '/display/glue/glue-info-by-glue-id',
        method: 'get',
        params: {
          glueId: that.glueId
        }
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res !== null) {
          that.glueVisible = true
          that.gluedata.push(res)
        }
      }).catch(function (err) {
        that.glueVisible = false
        if (err.response.status === 401) {
          alert('用户未登录或登录已过期')
          localStorage.clear()
          that.$router.push({path: '/user/login/'})
          window.location.reload()
        } else if (err.response.status === 403) {
          that.$message({
            type: 'error',
            message: '权限不够！'
          })
        } else if (err.response.status === 404) {
          that.$message({
            type: 'error',
            message: '未找到数据！'
          })
        } else if (err.response.status === 504) {
          that.$message({
            type: 'error',
            message: '服务器未响应！'
          })
        } else {
          that.$message({
            type: 'error',
            message: '创建失败！'
          })
        }
      })
    },
    // 创建胶水
    post_glue () {
      console.log(this.addGlueId)
      this.postdata = [...new Set(this.addGlueId.split(/[(\r\n)\r\n]+/))]
      console.log(this.addGlueId)
      console.log(this.postdata)
      this.errlist = []
      console.log(this.postdata[1])
      for (let i = 0; i < this.postdata.length; i++) {
        console.log(this.postdata[1])
        if (this.postdata[i] !== '') {
          let that = this
          console.log(that.postdata[i])
          let formData = new FormData()
          formData.append('glue_id', that.postdata[i])
          formData.append('created_time', that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss'))
          that.$axios({
            url: '/display/glue/glue-info',
            method: 'post',
            data: formData
          }).then(function (response) {
            console.log(response)
          }).catch(function () {
            that.$message({
              type: 'error',
              message: '创建失败的胶水：' + that.postdata[i]
            })
          })
        }
      }
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
    clearInterval(this.datatimer)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
body {
  text-align:center
}
.el-table .error-row {
  background:rgb(255, 0, 0);
  color:black;
}
.el-table .warning-row {
  background: rgb(147, 149, 150);
}
.el-table .success-row {
  background: rgb(152, 229, 149);
}
.el-table .info-row {
  background: #eff165;
}
</style>



