import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MaterialGlue from '@/components/materialglue/MaterialGlue'
import CompoundGlue from '@/components/materialglue/CompoundGlue'
import PregroupTest from '@/components/materialglue/PregroupTest'
import OSAEquipmentInformationEntry from '@/components/materialglue/OSAEquipmentInformationEntry'
import WebSerialApi from '@/components/materialglue/WebSerialApi'
import GroupPreAssemblyInputSystem from '@/components/materialglue/GroupPreAssemblyInputSystem'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/material',
      component: MaterialGlue,
      children: [
        {path: 'material_glue', component: MaterialGlue}
      ]
    }, {
      path: '/glue',
      component: CompoundGlue,
      children: [
        {path: 'compound_glue', component: CompoundGlue}
      ]
    }, {
      path: '/api',
      component: WebSerialApi,
      children: [
        {path: 'web_serial_api', component: WebSerialApi}
      ]
    }, {
      path: '/test',
      component: PregroupTest,
      children: [
        {path: 'pregroup_test', component: PregroupTest}
      ]
    }, {
      path: '/equipment',
      component: OSAEquipmentInformationEntry,
      children: [
        {path: 'osa_equipment_information_entry', component: OSAEquipmentInformationEntry}
      ]
    }, {
      path: '/group_pre_assembly',
      component: GroupPreAssemblyInputSystem,
      children: [
        {path: 'group_pre_assembly_input_system', component: GroupPreAssemblyInputSystem}
      ]
    }
  ]
})



