<template>
  <div class="OSAEquipmentInformationEntry">
    <h3>优博创数字化工厂——OSA设备信息录入</h3>
    <div style="border:1px solid black; height: 550px">
      <el-row>
        <el-col :span="10">
          <br>
          <el-form label-width="80px" size="mini" style="border-bottom: 1px solid black;height: 300px">
            <el-form-item label="台位编号">
              <el-input v-model="stationID" @keyup.enter.native="ID_Enter"></el-input>
            </el-form-item>
            <el-form-item label="工号">
              <el-input v-model="jobID" ref="job" @keyup.enter.native="job_Enter"></el-input>
            </el-form-item>
            <el-form-item label="PN">
              <el-input v-model="pn" ref="pn" @keyup.enter.native="pn_Enter"></el-input>
            </el-form-item>
            <el-form-item label="工序">
              <el-select @change="process_change" v-model="process" placeholder="请选择" ref="process">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="设备固资号">
              <el-input v-model="quickResponseCode" clearable @keyup.enter.native="get_code"></el-input>
            </el-form-item>
            <el-button @click="get_code" type="primary">设备校验</el-button>
          </el-form>
          <div style="height: 200px;">
            <h4>校验结果:</h4>
            <b :style="fontcolor">{{verificationResult}}</b>
          </div>
        </el-col>
        <el-col :span="14">
          <el-table
          :data="tableData"
          height="550"
          border
          style="width: 100%;border-left: 1px solid black">
            <el-table-column
              prop="name"
              label="设备类型">
            </el-table-column>
            <el-table-column
              prop="assets"
              label="固资号">
            </el-table-column>
            <el-table-column
              prop="maintenance"
              label="维修状态">
            </el-table-column>
            <el-table-column
              prop="repair"
              label="保养状态">
            </el-table-column>
            <el-table-column
              prop="state"
              label="状态">
            </el-table-column>
            <el-table-column
            label="操作"
            fixed="right"
            width="60">
            <template slot-scope="scope">
              <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index,tableData)"
              class="el-icon-delete"
              size="small"></el-button>
            </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
    <br>
    <el-button type="info" style="width: 150px">重置</el-button>
    <el-button @click="entry_button" type="primary" style="width: 150px">录入</el-button>
  </div>
</template>

<script>
export default {
  name: 'OSAEquipmentInformationEntry',
  components: {
  },
  mounted () {
    this.get_import_equipment()
  },
  methods: {
    // 输入台位编号后换行
    ID_Enter () {
      if (this.stationID !== '') {
        this.$refs['job'].focus()
      }
    },
    job_Enter () {
      if (this.jobID !== '') {
        this.$refs['pn'].focus()
      }
    },
    pn_Enter () {
      if (this.pn !== '') {
        this.$refs['process'].focus()
      }
    },
    // 获取重要设备
    get_import_equipment () {
      let that = this
      that.options = []
      that.$axios({
        url: '/display/osa-device-entry/osa-device-set',
        method: 'get'
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res !== null) {
          console.log(res)
          that.importEquipment = res
          var data = Array.from(Object.keys(res))
          console.log(data)
          for (let i = 0; i < data.length; i++) {
            that.options.push({'value': data[i], 'label': data[i]})
          }
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 设备校验
    get_code () {
      let that = this
      if (that.quickResponseCode !== '') {
        that.verificationResult = ''
        that.$axios({
          url: '/api/device-base/detail',
          method: 'get',
          params: {
            assets: that.quickResponseCode
          }
        }).then(function (response) {
          console.log(response)
          const res = response.data
          if (res !== null) {
            console.log(res)
            if ((res['status_of_maintenance'] === '正常' || res['status_of_maintenance'] === '未绑定') && (res['status_of_repair'] === '正常' || res['status_of_repair'] === '未绑定')) {
              that.fontcolor = 'color: green'
              console.log('通过')
              that.verificationResult = '通过'
              that.quickResponseCode = null
              that.tableData.push({'name': res['device_base_info']['category_child'], 'assets': res['device_base_info']['assets'], 'maintenance': res['status_of_maintenance'], 'repair': res['status_of_repair'], 'state': '已通过'})
              console.log(that.tableData)
            } else {
              that.fontcolor = 'color: red'
              if (res['status_of_maintenance'] !== '正常' && res['status_of_maintenance'] !== '未绑定') {
                that.verificationResult = '维修状态：' + res['status_of_maintenance'] + ';'
              }
              if (res['status_of_repair'] !== '正常' && res['status_of_repair'] !== '未绑定') {
                that.verificationResult = '保养状态：' + res['status_of_repair'] + ';'
              }
            }
          }
        }).catch(function (error) {
          console.log(error)
          that.$message({
            type: 'error',
            message: '获取设备校验信息失败！'
          })
        })
      }
    },
    process_change () {
      this.tableData = []
      console.log(this.process)
      console.log(this.importEquipment[this.process])
    },
    deleteRow (index, rows) {
      rows.splice(index, 1)
    },
    // 录入按钮
    entry_button () {
      console.log(this.tableData)
      if (this.tableData.length !== 0) {
        var data = JSON.parse(JSON.stringify(this.importEquipment[this.process]))
        for (let k = 0; k < this.tableData.length; k++) {
          for (let i = 0; i < data.length; i++) {
            if (data[i] === this.tableData[k]['name']) {
              data.splice(i, 1)
              i--
            }
          }
        }
        console.log(data)
        if (data.length === 0) {
          console.log('可以录入')
          this.get_entry()
        } else {
          this.$message({
            type: 'error',
            message: '重要设备信息未录入！'
          })
        }
      }
    },
    // 录入
    get_entry () {
      if (this.stationID !== '' && this.jobID !== '' && this.pn !== '' && this.process !== '') {
        var dataSN = []
        for (let i = 0; i < this.tableData.length; i++) {
          dataSN.push(this.tableData[i]['assets'])
        }
        dataSN = Array.from(new Set(dataSN))
        if (dataSN.length === this.tableData.length) {
          let that = this
          var listSN = ','
          for (let i = 0; i < that.tableData.length; i++) {
            if (i === 0) {
              listSN = that.tableData[i]['assets']
            } else {
              listSN = listSN + ',' + that.tableData[i]['assets']
            }
          }
          var newtime = that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
          let formData = new FormData()
          formData.append('station_id', that.stationID)
          formData.append('operator_id', that.jobID)
          formData.append('entry_time', newtime)
          formData.append('process', that.process)
          formData.append('pn', that.pn)
          formData.append('device_sn_list', listSN)
          that.$axios({
            url: '/display/osa-device-entry/osa-device-entry-info',
            method: 'post',
            data: formData
          }).then(function (response) {
            that.$message({
              type: 'success',
              message: '录入成功！'
            })
            that.tableData = []
          }).catch(function (error) {
            console.log(error)
            that.$message({
              type: 'error',
              message: '录入失败!'
            })
          })
        } else {
          this.$message({
            type: 'error',
            message: '有重复的设备信息！'
          })
        }
      } else {
        this.$message({
          type: 'error',
          message: '请输入基本信息！'
        })
      }
    }
  },
  data () {
    return {
      fontcolor: '',
      verificationResult: '',
      stationID: '',
      jobID: '',
      pn: '',
      quickResponseCode: '',
      tableData: [],
      process: '',
      options: [],
      importEquipment: []
    }
  }
}
</script>

<style scoped>

</style>



