<template>
  <div class="GroupPreAssemblyInputSystem">
    <h3>
      优博创数字化工厂——预组录入系统
      </h3>
    <div style="border: 1px solid black;margin:0 auto;width: 90%">
      <el-form ref="form" :rules="rules" :model="form" label-width="150px" size="mini">
        <el-form-item label="员工工号" prop="name">
          <el-input v-model="form.name" style="width:100%" @keyup.enter.native="oneSubmit">
            <template slot="prepend">JS</template>
          </el-input>
        </el-form-item>
        <el-form-item label="工单号">
          <el-input v-model="form.workOrder" ref="workOrder" style="width:69%" @blur="get_workOrder" @keyup.enter.native="get_workOrder"></el-input>
          <el-button style="background-color: #409eff; color: #fff;width:30%" @click="get_workOrder">工单匹配</el-button>
        </el-form-item>
        <el-form-item label="产品PN">
          <el-input v-model="form.pn" :disabled="true" style="width:100%"></el-input>
        </el-form-item>
        <el-form-item label="BOM">
          <el-input v-model="form.bom" :disabled="true" style="width:100%"></el-input>
        </el-form-item>
        <el-form-item label="台位编号">
          <el-input v-model="form.station" ref="station" style="width:100%"></el-input>
        </el-form-item>
        <el-form-item label="当前工序">
          <el-select v-model="form.process" @change="post_process" @focus="get_process" placeholder="请选择当前工序" style="width:100%">
            <el-option
            :disabled="process_select"
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物料代码">
          <el-tooltip class="item" effect="dark" placement="top"><div slot="content">{{form.materialCode}}</div>
            <el-input v-model="form.materialCode" @change="post_glue_id" :disabled="true" style="width: 69%"></el-input>
          </el-tooltip>
          <el-button style="background-color: #409eff; color: #fff;width:30%" @click="put_material_code">添加物料代码</el-button>
        </el-form-item>
        <el-form-item label="物料批次号">
          <el-tooltip class="item" effect="dark" placement="top"><div slot="content">{{form.materialLot}}</div>
          <el-input v-model="form.materialLot" :disabled="true" style="width: 69%"></el-input>
          </el-tooltip>
          <el-button style="background-color: #409eff; color: #fff;width:30%" @click="put_material_log">添加物料批次</el-button>
        </el-form-item>
        <el-form-item label="胶水ID">
          <el-input v-model="form.glueId" :disabled="idjudge" style="width: 69%" @change="get_glue_aging"></el-input>
          <el-button v-if="effectivePrescription" style="background-color: #67C23A; color: #fff;width:30%;">时效：{{time}} 秒</el-button>
          <el-button v-else style="background-color: red; color: #fff;width:30%;">时效：0 秒</el-button>
        </el-form-item>
        <!-- <el-form-item label="工作开始时间">
          <el-input v-model="form.startTime" :disabled="true" style="width: 69%"></el-input>
          <el-button style="background-color: #409eff; color: #fff;width:30%" @click="get_start_time">开始</el-button>
        </el-form-item> -->
        <!-- <el-form-item label="SN">
          <el-tooltip class="item" effect="dark" placement="top"><div slot="content">{{form.sn}}</div>
          <el-input v-model="form.sn" :disabled="true" style="width: 69%"></el-input>
          </el-tooltip>
          <el-popover
            placement="right"
            width="300"
            title="SN"
            trigger="manual"
            v-model="visible">
            <el-input v-model="textareaSn" type="textarea" :autosize="{ minRows: 1, maxRows: 4}"></el-input>
            <el-button style="background-color: #409eff; color: #fff;width:30%" @click="confirm_add">确认添加</el-button>
            <el-button slot="reference" :disabled="addSn" style="background-color: #409eff; color: #fff;width:30%" @click="visible = !visible">添加SN</el-button>
          </el-popover>
        </el-form-item> -->
        <el-form-item label="本次数量">
          <el-input v-model="form.totalCount" :disabled="total_ount" style="width: 100%"></el-input>
        </el-form-item>
        <el-form-item label="不良数量">
          <el-input v-model.number="form.failCount" :min="0" :max="form.totalCount" style="width: 100%"></el-input>
        </el-form-item>
        <el-form-item label="已录入数">
          <el-input v-model.number="form.addUpNumber" :min="0" style="width: 100%" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="已录入不良">
          <el-input v-model.number="form.addDownNumber" :min="0" style="width: 100%" :disabled="true"></el-input>
        </el-form-item>
        <!-- <el-form-item label="工作结束时间">
          <el-input v-model="form.endTime" :disabled="true" style="width: 69%"></el-input>
          <el-button style="background-color: #409eff; color: #fff;width:30%" @click="get_end_time">停止</el-button>
        </el-form-item> -->
        <el-form-item>
          <el-row :gutter="20">
            <el-col :span="10">
              <el-button type="primary" @click="clear_form">重置</el-button>
            </el-col>
            <el-col :span="10">
              <el-button type="primary" @click="post_entry('form')">录入</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
    </div>
    <!--添加关键物料弹窗-->
    <el-dialog
    title="添加关键物料"
    :visible.sync="addmaterialdialogVisible"
    width="80%">
      <el-dialog
      title="tco验证"
      :visible.sync="tcodialogVisible"
      width="80%"
      append-to-body>
      <el-form label-width="120px">
        <el-form-item label="请输入密码：">
          <el-input @keyup.enter.native="get_k3_password" v-model="k3Password" style="width: 68%" show-password></el-input>
          <el-button @click="get_k3_password" style="width: 30%">确认</el-button>
        </el-form-item>
      </el-form>
      </el-dialog>
      <el-form :model="keyMaterialValidateForm" label-width="120px" class="demo-dynamic">
        <el-form-item label="是否开启K3检验(TCO)">
          <el-switch
            v-model="tcavalue"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text="开"
            @change="open_k3_check"
            inactive-text="关">
          </el-switch>
        </el-form-item>
        <el-form-item
          v-for="(domain, index) in keyMaterialValidateForm.domains"
          :label="keyMaterialValidateForm.domains[index].key"
          :key="domain.key"
          :prop="'domains.' + index + '.value'"
          :rules="{
            required: true, message: '不能为空', trigger: 'blur'
          }"
        >
        <el-input type="textarea" v-model="domain.value"></el-input>
        </el-form-item>
      </el-form>
      <el-button @click="get_Verification">验证</el-button>
      <el-dialog
        width="30%"
        title="未验证通过的物料"
        :visible.sync="materialsThatHaveNotPassedValidationVisible"
        append-to-body>
        <p>{{text}}</p>
      </el-dialog>
    </el-dialog>
    <!--添加物料批次弹窗-->
    <el-dialog
    title="添加物料批次"
    :visible.sync="addMaterialLotDialogVisible"
    width="80%">
      <el-form :model="keyMaterialLogValidateForm" label-width="130px" class="demo-dynamic">
        <el-form-item
          v-for="(data, index) in keyMaterialLogValidateForm.data"
          :label="keyMaterialLogValidateForm.data[index].key"
          :key="data.key"
          :prop="'data.' + index + '.value'"
          :rules="{
            required: true, message: '不能为空', trigger: 'blur'
          }"
        >
        <el-input v-model="data.value" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <el-button @click="get_add_material_log">确认添加</el-button>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'GroupPreAssemblyInputSystem',
  components: {
  },
  watch: {
    errorText: {
      handler: function (newVal, oldVal) {
        if (newVal === true) {
          setTimeout(() => {
            this.errorText = false
          }, 1000 * 60)
        }
      }
    }
    // effectivePrescription: {
    //   handler: function (newVal, oldVal) {
    //     if (newVal !== oldVal) {
    //       if (newVal === false) {
    //         this.get_close()
    //       } else if (newVal === true) {
    //         this.get_open()
    //       }
    //     }
    //   }
    // }
  },
  mounted () {
    clearInterval(this.timer)
    clearInterval(this.timer2)
    this.get_all_process()
    this.get_loacl_storage()
    // this.web_socket_init()
  },
  methods: {
    // 初始化websocket连接
    // web_socket_init () {
    //   if ('WebSocket' in window) {
    //     let that = this
    //     that.ws = new WebSocket('ws://127.0.0.1:8003')
