    //     that.ws.onmessage = function (e) {
    //       console.log(e)
    //       if (e.data === '连接脚本成功') {
    //         that.$message({
    //           type: 'success',
    //           message: '连接脚本成功!'
    //         })
    //         that.serialText = '连接脚本成功，正在连接串口...'
    //         that.tagType = 'success'
    //       } else if (e.data === '自动选择正确的串口成功') {
    //         that.serialText = '连接串口成功'
    //         that.tagType = 'success'
    //       } else if (e.data === '没有连接正确的串口') {
    //         that.serialText = '未连接有效串口'
    //         that.tagType = 'danger'
    //       } else if (e.data === '开门成功') {
    //         that.$message({
    //           type: 'success',
    //           message: '开门成功!'
    //         })
    //         that.serialText = '已开门'
    //         that.tagType = 'success'
    //       } else if (e.data === '开门失败') {
    //         that.$message({
    //           type: 'success',
    //           message: '开门失败!'
    //         })
    //         that.serialText = '开门失败'
    //         that.tagType = 'danger'
    //       } else if (e.data === '关门成功') {
    //         that.$message({
    //           type: 'success',
    //           message: '关门成功!'
    //         })
    //         that.serialText = '已关门'
    //         that.tagType = 'success'
    //       } else if (e.data === '关门失败') {
    //         that.$message({
    //           type: 'success',
    //           message: '关门失败!'
    //         })
    //         that.serialText = '关门失败'
    //         that.tagType = 'danger'
    //       }
    //     }
    //     that.ws.onerror = function (e) {
    //       if (e.type === 'error') {
    //         if (that.errorText === false) {
    //           that.$message({
    //             type: 'error',
    //             message: '未连接有效串口！'
    //           })
    //           that.errorText = true
    //         }
    //         // that.get_new()
    //       }
    //     }
    //     that.ws.onclose = function (e) {
    //       console.log(e)
    //       that.serialText = '脚本连接失败！'
    //       that.tagType = 'danger'
    //       // that.get_new()
    //       // that.get_new()
    //     }
    //     clearInterval(that.timer2)
    //     that.timer2 = setInterval(() => {
    //       that.ws.send('自动选择串口')
    //     }, 1000 * 10)
    //   } else {
    //     this.$message({
    //       type: 'error',
    //       message: '您的浏览器不支持WebSocket通信！'
    //     })
    //     this.serialText = '您的浏览器不支持WebSocket通信'
    //   }
    // },
    // get_new () {
    //   this.web_socket_init()
    // },
    // get_open () {
    //   let that = this
    //   that.ws.send('开门')
    // },
    // get_close () {
    //   let that = this
    //   that.ws.send('关门')
    // },
    // 获取员工工号
    oneSubmit () {
      this.$refs['workOrder'].focus()
    },
    // 获取缓存中的数据
    get_loacl_storage () {
      if (localStorage.getItem('workerData') !== null) {
        console.log('workerData', JSON.parse(localStorage.getItem('workerData')))
        this.form = JSON.parse(localStorage.getItem('workerData'))
        this.get_glue_aging()
        this.get_add_data()
      }
    },
    // 获取累计数据
    get_add_data () {
      let that = this
      const Prefix = 'JS' + that.form.name
      that.$axios({
        url: '/display/entry/prepare-assemble-product-output-number',
        method: 'get',
        params: {
          workOrder: that.form.workOrder,
          worker: Prefix,
          process: that.form.process
        }
      }).then(function (response) {
        console.log('累计总数：', response)
        const res = response.data
        if (res !== null) {
          that.form.addUpNumber = res
        }
      }).catch(function (err) {
        console.log(err)
        that.$message({
          type: 'error',
          message: '获取累计总数失败！'
        })
      })
      that.$axios({
        url: '/display/entry/prepare-assemble-product-bad-output-number',
        method: 'get',
        params: {
          workOrder: that.form.workOrder,
          worker: Prefix,
          process: that.form.process
        }
      }).then(function (response) {
        console.log('累计不良：', response)
        const res = response.data
        if (res !== null) {
          that.form.addDownNumber = res
        }
      }).catch(function (err) {
        console.log(err)
        that.$message({
          type: 'error',
          message: '获取累计不良数失败！'
        })
      })
    },
    // 重置form表单
    clear_form () {
      this.form = {
        name: '',
        workOrder: '',
        pn: '',
        bom: '',
        station: '',
        process: '',
        materialCode: '',
        materialLot: '',
        glueId: '',
        startTime: '',
        endTime: '',
        sn: '',
        totalCount: '0',
        failCount: '0',
        tco: '1',
        addUpNumber: '0',
        addDownNumber: '0'
      }
    },
    // 通过工单号进行匹配
    get_workOrder () {
      console.log(this.form.workOrder)
      if (this.form.name !== '') {
        if (this.form.workOrder === '') {
          this.$message({
            type: 'error',
            message: '未输入工单号!'
          })
        } else {
          let that = this
          that.$axios({
            url: '/display/entry/pn-and-bom-by-worker-order',
            method: 'get',
            params: {
              workOrder: that.form.workOrder
            }
          }).then(function (response) {
            console.log(response)
            const res = response.data
            if (res !== null) {
              that.form.pn = res.Pn
              that.form.bom = res.Bom
              that.$refs['station'].focus()
            }
          }).catch(function () {
            this.$message({
              type: 'error',
              message: '获取数据失败！'
            })
          })
        }
      } else {
        this.$message({
          type: 'error',
          message: '未输入员工工号！'
        })
      }
    },
    // 通过工单号查询k3中的物料代码
    get_k3 () {
      let that = this
      that.$axios({
        url: '/display/entry/k3-material-by-work-order',
        method: 'get',
        params: {
          workOrder: that.form.workOrder
        }
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res !== null) {
          that.k3_data = res
        }
      }).catch(function () {
        this.$message({
          type: 'error',
          message: '获取数据失败！'
        })
      })
    },
    // 选择工序前判断是否输入台位编号
    get_process () {
      if (this.form.station === '') {
        this.process_select = true
        this.$message({
          type: 'error',
          message: '请先输入台位编号！'
        })
      } else {
        this.process_select = false
      }
    },
    // 获取当前工序
    get_all_process () {
      let that = this
      that.options = []
      that.$axios({
        url: '/display/entry/all-process',
        method: 'get'
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res !== null) {
          for (let i = 0; i < res.length; i++) {
            console.log(res[i])
            that.options.push({value: res[i], label: res[i]})
          }
        }
      }).catch(function () {
        this.$message({
          type: 'error',
          message: '获取数据失败！'
        })
      })
    },
    // 修改当前工序
    post_process () {
      this.form.materialCode = ''
      this.form.materialLot = ''
      this.tcavalue = true
      this.keyMaterialValidateForm.domains = []
      this.keyMaterialLogValidateForm.data = []
    },
    // 添加物料代码
    put_material_code () {
      let that = this
      if (that.form.materialCode === '') {
        that.keyMaterialValidateForm.domains = []
      }
      if (that.form.workOrder !== '' && that.form.process !== '') {
        if (this.form.materialCode === '') {
          that.keyMaterial = []
          that.$axios({
            url: '/display/entry/k3-material-of-process-by-process',
            method: 'get',
            params: {
              process: that.form.process
            }
          }).then(function (response) {
            console.log(response)
            const res = response.data
            if (res !== null) {
              var listvalues = Object.values(res)
              var listkeys = Object.keys(res)
              console.log(listvalues)
              console.log(listkeys)
              for (let j = 0; j < listvalues.length; j++) {
                if (listvalues[j] === 1 && listkeys[j] !== 'Id') {
                  console.log(listkeys[j])
                  that.keyMaterial.push(listkeys[j])
                  that.$axios({
                    url: '/display/entry/key-material-prefix-code-by-material',
                    method: 'get',
                    params: {
                      materialNickname: listkeys[j]
                    }
                  }).then(function (response) {
                    console.log(response)
                    const res = response.data
                    if (res !== null) {
                      console.log(res[0]['Material'])
                      that.keyMaterialValidateForm.domains.push({key: res[0]['Material'], name: listkeys[j], value: ''})
                    }
                  })
                }
              }
              that.addmaterialdialogVisible = true
            }
          }).catch(function () {
            this.$message({
              type: 'error',
              message: '获取数据失败！'
            })
          })
        } else {
          that.addmaterialdialogVisible = true
        }
      } else if (that.form.workOrder === '') {
        that.$message({
          type: 'error',
          message: '请先完成工单匹配！'
        })
      } else if (that.form.workOrder !== '' && that.form.process === '') {
        that.$message({
          type: 'error',
          message: '请先选择当前工序！'
        })
      }
    },
    // 是否开起k3校验
    open_k3_check () {
      console.log(this.tcavalue)
      if (this.tcavalue === false) {
        this.tcavalue = true
        this.tcodialogVisible = true
        this.$message({
          type: 'warning',
          message: '请先通过密码校验！'
        })
      } else if (this.tcavalue === true) {
        this.$message({
          type: 'success',
          message: '已开起K3校验！'
        })
      }
    },
    // k3密码检验
    get_k3_password () {
      if (this.k3Password === '85200') {
        this.tcavalue = false
        this.tcodialogVisible = false
        this.$message({
          type: 'success',
          message: '校验成功，已关闭k3校验！'
        })
      } else {
        this.$message({
          type: 'error',
          message: '校验失败,请输入正确的密码！'
        })
      }
    },
    // 验证
    get_Verification () {
      console.log(this.tcavalue)
      if (this.tcavalue === true) {
        // 开启K3校验
        this.form.tco = '0'
        if (this.form.workOrder !== '') {
          let that = this
          var openlist = []
          for (let q = 0; q < that.keyMaterialValidateForm.domains.length; q++) {
            openlist = openlist.concat(that.keyMaterialValidateForm.domains[q].value.split(/[(\r\n)\r\n]+/))
          }
          if (new Set(openlist).size !== openlist.length) {
            that.$message({
              type: 'error',
              message: '存在重复的物料代码,请重新验证！'
            })
          } else {
            that.post_glue_id()
            that.errkey = []
            that.$axios({
              url: '/display/entry/k3-material-by-work-order',
              method: 'get',
              params: {
                workOrder: that.form.workOrder
              }
            }).then(function (response) {
              console.log(response)
              const res = response.data
              if (res !== null) {
                that.k3_data = res
                console.log(that.keyMaterialValidateForm.domains.length)
                if (that.keyMaterialValidateForm.domains.length > 0) {
                  for (let x = 0; x < that.keyMaterialValidateForm.domains.length; x++) {
                    const err = [...new Set(that.keyMaterialValidateForm.domains[x].value.split(/[(\r\n)\r\n]+/))]
                    console.log(err)
                    for (let i = 0; i < err.length; i++) {
                      if (err[i] === '') {
                        err.splice(i, 1)
                        i--
                      }
                    }
                    that.$axios({
                      url: '/display/entry/key-material-prefix-code-by-material',
                      method: 'get',
                      params: {
                        materialNickname: that.keyMaterialValidateForm.domains[x].name
                      }
                    }).then(function (response) {
                      console.log(response)
                      const res = response.data[0]
                      if (res !== null) {
                        console.log(res['PrefixCode'])
                        var list = res['PrefixCode'].split(',')
                        for (let y = 0; y < err.length; y++) {
                          if (err[y].length < 8) {
                            that.errkey.push(err[y])
                          } else {
                            var k = true
                            for (let i = 0; i < list.length; i++) {
                              if (err[y].substring(0, 8) === list[i]) {
                                k = false
                              }
                            }
                            if (k === true) {
                              that.errkey.push(err[y])
                            } else {
                              let truekey = 0
                              console.log(that.k3_data[k])
                              console.log(err[y])
                              for (let k = 0; k < that.k3_data.length; k++) {
                                if (that.k3_data[k] === err[y]) {
                                  truekey = 1
                                }
                              }
                              if (truekey === 0) {
                                that.errkey.push(err[y])
                              }
                            }
                          }
                        }
                        if (that.errkey.length > 0) {
                          console.log(that.errkey)
                          that.materialsThatHaveNotPassedValidationVisible = true
                          that.text = ''
                          for (let q = 0; q < that.errkey.length; q++) {
                            if (q === 0) {
                              that.text = that.text + that.errkey[q]
                            } else {
                              that.text = that.text + '和' + that.errkey[q]
                            }
                          }
                        } else {
                          that.$message({
                            type: 'success',
                            message: '验证通过！'
                          })
                          that.post_glue_id()
                          var materialCode = ''
                          for (let w = 0; w < that.keyMaterialValidateForm.domains.length; w++) {
                            const err = [...new Set(that.keyMaterialValidateForm.domains[w].value.split(/[(\r\n)\r\n]+/))]
                            console.log(err)
                            for (let e = 0; e < err.length; e++) {
                              if (err[e] !== '') {
                                materialCode = materialCode + that.keyMaterialValidateForm.domains[w].key + ':' + err[e] + ';'
                              }
                            }
                          }
                          that.form.materialCode = materialCode
                          console.log(that.form.materialCode)
                          console.log(that.keyMaterialValidateForm.domains)
                          that.addmaterialdialogVisible = false
                        }
                      }
                    })
                  }
                }
              }
            }).catch(function () {
              this.$message({
                type: 'error',
                message: '获取数据失败！'
              })
            })
          }
          console.log(that.keyMaterialValidateForm)
        }
      } else {
        // 关闭K3校验
        this.form.tco = '1'
        let that = this
        var materialCode = ''
        var list = []
        for (let q = 0; q < that.keyMaterialValidateForm.domains.length; q++) {
          list = list.concat(that.keyMaterialValidateForm.domains[q].value.split(/[(\r\n)\r\n]+/))
        }
        if (new Set(list).size !== list.length) {
          that.$message({
            type: 'error',
            message: '存在重复的物料代码,请重新验证！'
          })
        } else {
          that.post_glue_id()
          that.$message({
            type: 'success',
            message: '验证通过！'
          })
          for (let w = 0; w < that.keyMaterialValidateForm.domains.length; w++) {
            var err = that.keyMaterialValidateForm.domains[w].value.split(/[(\r\n)\r\n]+/)
            console.log(err)
            for (let e = 0; e < err.length; e++) {
              if (err[e] !== '') {
                materialCode = materialCode + that.keyMaterialValidateForm.domains[w].key + ':' + err[e] + ';'
              }
            }
            console.log(materialCode)
          }
          that.form.materialCode = materialCode
          console.log(that.form.materialCode)
          console.log(that.keyMaterialValidateForm.domains)
          that.addmaterialdialogVisible = false
        }
      }
    },
    // 修改物料代码
    post_glue_id () {
      this.form.materialLot = ''
      this.keyMaterialLogValidateForm.data = []
    },
    // 添加物料批次
    put_material_log () {
      let that = this
      // that.keyMaterialLogValidateForm.data = []
      if (that.form.materialCode !== '') {
        if (that.keyMaterialLogValidateForm.data.length === 0) {
          for (let w = 0; w < that.keyMaterialValidateForm.domains.length; w++) {
            var err = that.keyMaterialValidateForm.domains[w].value.split(/[(\r\n)\r\n]+/)
            console.log(err)
            for (let e = 0; e < err.length; e++) {
              if (err[e] !== '') {
                that.keyMaterialLogValidateForm.data.push({key: err[e], value: ''})
                console.log(that.keyMaterialLogValidateForm.data)
              }
            }
          }
        }
        that.addMaterialLotDialogVisible = true
      } else {
        that.$message({
          type: 'error',
          message: '请先添加物料代码！'
        })
      }
    },
    // 确认添加物料批次
    get_add_material_log () {
      let that = this
      console.log(that.keyMaterialLogValidateForm)
      var copydata = JSON.parse(JSON.stringify(that.keyMaterialLogValidateForm))
      var copydatatwo = JSON.parse(JSON.stringify(that.keyMaterialLogValidateForm))
      console.log(copydata)
      var materialLog = ''
      var codelist = []
      var judge = false
      for (let q = 0; q < copydata.data.length; q++) {
        codelist = codelist.concat(copydata.data[q].value.split(/[(\r\n)\r\n]+/))
      }
      console.log(codelist)
      if (new Set(codelist).size !== codelist.length) {
        that.$message({
          type: 'error',
          message: '存在重复的物料代码,请重新验证！'
        })
      } else {
        that.post_glue_id()
        that.$message({
          type: 'success',
          message: '验证通过！'
        })
        judge = true
        // for (let w = 0; w < codelist.length; w++) {
        //   if (codelist[w] !== '') {
        //     materialLog = materialLog + codelist[w] + ':' + w + ';'
        //   }
        // }
        // console.log('物料批次', that.keyMaterialLogValidateForm.data)
      }
      console.log(copydatatwo)
      if (judge === true) {
        for (let w = 0; w < copydatatwo.data.length; w++) {
          const err = copydatatwo.data[w].value.split(/[(\r\n)\r\n]+/)
          console.log(err)
          for (let e = 0; e < err.length; e++) {
            if (err[e] !== '') {
              materialLog = materialLog + copydatatwo.data[w].key + ':' + err[e] + ';'
            }
          }
        }
        that.form.materialLot = materialLog
        console.log(that.form.materialLot)
        that.addMaterialLotDialogVisible = false
      }
    },
    // 获取胶水时效
    get_glue_aging () {
      let that = this
      if (that.form.materialLot !== '') {
        if (that.form.glueId !== '') {
          clearInterval(that.timer)
          that.effectivePrescription = false
          that.$axios({
            url: '/display/glue/glue-info-by-glue-id',
            method: 'get',
            params: {
              glueId: that.form.glueId
            }
          }).then(function (response) {
            console.log(response)
            const res = response.data
            if (res !== null) {
              console.log(res['CreatedTime'])
              console.log(new Date(res['CreatedTime']))
              var newTime = that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
              console.log(newTime)
              if ((new Date() - new Date(res['CreatedTime'])) >= 4800000 || res['RecycleFlag'] === 1) {
                that.$alert('当前胶水已超出时效或已回收!', '警告', {
                  confirmButtonText: '确定'
                })
                console.log('超出时效')
                console.log((new Date() - new Date(res['CreatedTime'])) / 1000)
              } else {
                console.log('未超出时效')
                that.time = ((new Date(res['CreatedTime']) - new Date() + 4800000) / 1000).toFixed(0)
                that.effectivePrescription = true
                // 开启定时器
                that.timer = setInterval(() => {
                  if ((new Date() - new Date(res['CreatedTime'])) >= 4800000 || res['RecycleFlag'] === 1) {
                    console.log('超出时效')
                    that.effectivePrescription = false
                    clearInterval(that.timer)
                  } else {
                    console.log('未超出时效')
                    that.effectivePrescription = true
                    that.time = ((new Date(res['CreatedTime']) - new Date() + 4800000) / 1000).toFixed(0)
                  }
                }, 1000)
              }
            }
          }).catch(function () {
            that.$message({
              type: 'error',
              message: '无此胶水ID！'
            })
          })
          // that.get_start_time()
        }
      } else {
        that.$message({
          type: 'error',
          message: '未添加物料批次号，无法获取胶水时效！'
        })
      }
    },
    // 获取工作开始时间
    // get_start_time () {
    //   if (this.form.materialLot !== '') {
    //     if (this.effectivePrescription === true && this.form.glueId !== '') {
    //       this.$message({
    //         type: 'success',
    //         message: '开始工作！'
    //       })
    //       this.idjudge = true
    //       this.addSn = false
    //       this.total_ount = false
    //       this.form.startTime = this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
    //     } else if (this.effectivePrescription === false && this.form.glueId === '') {
    //       this.$message({
    //         type: 'success',
    //         message: '未使用胶水,开始工作！'
    //       })
    //       this.idjudge = true
    //       this.addSn = false
    //       this.total_ount = false
    //       this.form.startTime = this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
    //     } else {
    //       this.$message({
    //         type: 'error',
    //         message: '无法开始工作！'
    //       })
    //     }
    //   } else {
    //     this.$message({
    //       type: 'error',
    //       message: '请先录入批次号！'
    //     })
    //   }
    // },
    // 获取结束时间
    // get_end_time () {
    //   if (this.form.endTime === '') {
    //     if (this.form.startTime === '') {
    //       this.$message({
    //         type: 'error',
    //         message: '未开始工作，无法获得停止结束工作时间！'
    //       })
    //     } else {
    //       this.$message({
    //         type: 'success',
    //         message: '已结束工作！'
    //       })
    //       this.addSn = true
    //       this.visible = false
    //       this.total_ount = false
    //       this.form.endTime = this.moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
    //     }
    //   } else {
    //     this.$message({
    //       type: 'error',
    //       message: '无法重复停止工作！'
    //     })
    //   }
    // },
    // 添加SN
    confirm_add () {
      if (this.form.startTime !== '') {
        var sn = this.textareaSn.split(/[(\r\n)\r\n]+/)
        if (new Set(sn).size !== sn.length) {
          this.$message({
            type: 'error',
            message: '有重复的sn，请重新添加sn！'
          })
        } else {
          var formsn = ''
          var number = 0
          console.log(sn)
          if (sn.length > 0) {
            for (let s = 0; s < sn.length; s++) {
              if (sn[s] !== '') {
                if (s !== 0) {
                  formsn = formsn + ';' + sn[s]
                  number += 1
                } else {
                  formsn = formsn + sn[s]
                  number += 1
                }
              }
            }
          }
          console.log(formsn)
          console.log(number)
          this.form.totalCount = number
          this.form.sn = formsn
          this.total_ount = false
        }
      } else {
        this.$message({
          type: 'error',
          message: '请先开始工作,再进行添加SN！'
        })
      }
      // this.visible = !this.visible
    },
    // 录入
    post_entry (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.form.pn !== '' || this.form.bom !== '') {
            if (this.form.totalCount >= this.form.failCount) {
              let that = this
              let formData = new FormData()
              const Prefix = 'JS' + that.form.name
              formData.append('worker_id', Prefix)
              formData.append('work_order_num', that.form.workOrder)
              formData.append('pn', that.form.pn)
              formData.append('bom', that.form.bom)
              formData.append('station_id', that.form.station)
              formData.append('process', that.form.process)
              formData.append('item_code', that.form.materialCode)
              formData.append('material_num', that.form.materialLot)
              formData.append('glue_id', that.form.glueId)
              formData.append('start_time', that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss'))
              formData.append('end_time', that.moment(new Date()).format('YYYY-MM-DD HH:mm:ss'))
              formData.append('sn', that.form.sn)
              formData.append('total_count', that.form.totalCount)
              formData.append('fail_count', that.form.failCount)
              formData.append('tco', that.form.tco)
              that.$axios({
                url: '/display/entry/prepare-assemble-product-record',
                method: 'post',
                data: formData
              }).then(function (response) {
                if (response.status === 200) {
                  that.$message({
                    type: 'success',
                    message: '录入成功!'
                  })
                }
                var workerData = {
                  name: that.form.name,
                  workOrder: that.form.workOrder,
                  pn: that.form.pn,
                  bom: that.form.bom,
                  station: that.form.station,
                  process: that.form.process,
                  materialCode: that.form.materialCode,
                  materialLot: that.form.materialLot,
                  glueId: that.form.glueId,
                  startTime: '',
                  endTime: '',
                  sn: that.form.sn,
                  totalCount: that.form.totalCount,
                  failCount: that.form.failCount,
                  tco: that.form.tco,
                  addUpNumber: that.form.addUpNumber,
                  addDownNumber: that.form.addDownNumber
                }
                localStorage.setItem('workerData', JSON.stringify(workerData))
                setTimeout(() => {
                  location.reload()
                }, 2000)
              }).catch(function () {
                that.$message({
                  type: 'error',
                  message: '录入失败！'
                })
              })
            } else {
              this.$message({
                type: 'error',
                message: '不良总数大于生产总数,无法录入！'
              })
            }
          } else {
            this.$message({
              type: 'error',
              message: '未进行工单匹配，无法录入！'
            })
          }
        } else {
          this.$message({
            type: 'error',
            message: '数据有误，无法录入！'
          })
        }
      })
    }
  },
  // 20211026065
  // 21260059
  beforeDestroy () {
    clearInterval(this.timer)
    clearInterval(this.timer2)
  },
  data () {
    return {
      form: {
        name: '',
        workOrder: '',
        pn: '',
        bom: '',
        station: '',
        process: '',
        materialCode: '',
        materialLot: '',
        glueId: '',
        startTime: '',
        endTime: '',
        sn: '',
        totalCount: '0',
        failCount: '0',
        tco: '1',
        addUpNumber: '0',
        addDownNumber: '0'
      },
      rules: {
        name: [
          {required: true, message: '请输入工号(数字)', trigger: 'blur'},
          {min: 2, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur'},
          {
            validator: function (rule, value, callback) {
              if (/^[0-9]{2,50}$/.test(value) === false) {
                callback(new Error('请输入工号(数字)'))
              } else {
                // 校验通过
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      },
      keyMaterialValidateForm: {
        domains: []
      },
      keyMaterialLogValidateForm: {
        data: []
      },
      tagType: 'info',
      errorText: false,
      serialText: '',
      time: 4800000,
      text: '',
      k3Password: '',
      textareaSn: '',
      k3_data: [],
      errkey: [],
      options: [],
      keyMaterial: [],
      idjudge: false,
      total_ount: false,
      addSn: true,
      tcavalue: true,
      visible: false,
      process_select: true,
      effectivePrescription: false,
      tcodialogVisible: false,
      addmaterialdialogVisible: false,
      addMaterialLotDialogVisible: false,
      materialsThatHaveNotPassedValidationVisible: false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  text-align:center
}
</style>



