<template>
  <div class="PregroupTest">
    <div style="border: 1px solid black;width: 100%">
      <div style="margin:0 auto;width: 100%">
        <h2>查询装配结果</h2>
        <el-input ref="mark" @focus="getInputFocus($event)" clearable placeholder="请输入工单号" style="width:30%" v-model="workID" @keyup.enter.native="get_workOrder"></el-input>
        <el-button style="background-color: #409eff; color: #fff;width:80px" icon="el-icon-search" @click="get_workOrder"></el-button>
      </div>
      <br>
      <div style="text-align:justify;"><!--style="text-align:justify;"-->
      <el-row>
        <el-col :span="3" :offset="1">
          <b>需装配：</b>
        </el-col>
        <el-col :span="20">
          <span v-for="(item,index) in needAssemble" :key="index">
            <el-tag style="margin-right: 5px">{{item}}</el-tag>
          </span>
        </el-col>
      </el-row>
      <br>
      <el-row>
        <el-col :span="3" :offset="1">
          <b>已装配：</b>
        </el-col>
        <el-col :span="20">
          <span v-for="(item,index) in hasAssemble" :key="index">
            <el-tag type="success" style="margin-right: 5px">{{item}}</el-tag>
          </span>
        </el-col>
      </el-row>
      <br>
      <el-row>
        <el-col :span="3" :offset="1">
          <b>未装配：</b>
        </el-col>
        <el-col :span="20">
          <span v-for="(item,index) in NoAssemble" :key="index">
            <el-tag type="danger" style="margin-right: 5px">{{item}}</el-tag>
          </span>
        </el-col>
      </el-row>
      <br>
      <el-row>
        <el-col :span="3" :offset="1">
          <b>装配结果：</b>
        </el-col>
        <el-col :span="20">
          <b>{{resultAssemble}}</b>
        </el-col>
      </el-row>
      </div>
    </div>
    <el-dialog
    title="提示"
    :visible.sync="dialogVisible"
    top="20%"
    width="30%">
    <h2><i style="width:50px;color:red" class="el-icon-warning-outline"></i>{{resultAssemble}}</h2>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'PregroupTest',
  components: {
  },
  data () {
    return {
      workID: '',
      resultAssemble: '',
      needAssemble: [],
      hasAssemble: [],
      NoAssemble: [],
      dialogVisible: false
    }
  },
  mounted () {
  },
  watch: {
    dialogVisible: {
      handler: function (newVal, oldVal) {
        if (newVal === false) {
          this.$refs['mark'].focus()
        }
      }
    }
  },
  methods: {
    // input获得焦点时触发
    getInputFocus (event) {
      event.currentTarget.select()
    },
    // 通过工单号到k3中查询
    get_workOrder () {
      let that = this
      if (that.workID !== '') {
        that.resultAssemble = ''
        that.needAssemble = []
        that.hasAssemble = []
        that.NoAssemble = []
        that.get_k3_work()
      } else {
        that.$message({
          type: 'error',
          message: '未输入工单号！'
        })
      }
    },
    get_k3_work () {
      let that = this
      that.$axios({
        method: 'get',
        url: '/display/entry/k3-material-by-work-order',
        params: {
          workOrder: that.workID
        }
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res.length !== 0) {
          var needdata = []
          for (let i = 0; i < res.length; i++) {
            // text = text + res[i] + '<br>'
            needdata.push(res[i])
          }
          that.get_has_work()
          that.needAssemble = needdata
        } else {
          that.$message({
            type: 'error',
            message: '此工单暂无数据！'
          })
        }
      }).catch(function (err) {
        if (err.response.status === 401) {
          alert('用户未登录或登录已过期')
          localStorage.clear()
          that.$router.push({path: '/user/login/'})
          window.location.reload()
        } else if (err.response.status === 403) {
          that.$message({
            type: 'error',
            message: '权限不够！'
          })
        } else if (err.response.status === 404) {
          that.$message({
            type: 'error',
            message: '未找到数据！'
          })
        } else if (err.response.status === 504) {
          that.$message({
            type: 'error',
            message: '服务器未响应！'
          })
        } else {
          that.$message({
            type: 'error',
            message: '查询失败！'
          })
        }
      })
    },
    get_has_work () {
      let that = this
      that.$axios({
        method: 'get',
        url: '/display/check/all-material-of-prepare-assemble-product-record-by-worker-order',
        params: {
          workOrderNum: that.workID
        }
      }).then(function (response) {
        console.log(response)
        const res = response.data
        if (res.length !== 0) {
          if (res.length === 1 && res[0] === 'tco') {
            that.dialogVisible = true
            // setTimeout(function () {
            //   that.dialogVisible = false
            // }, 3000)
            // that.$confirm('当前为TCO工单', '提示', {
            //   type: 'warning',
            //   center: true,
            //   closeOnPressEscape: false,
            //   beforeClose: (action, instance, done) => {
            //     if (action === 'confirm') {
            //       instance.$refs['confirm'].$el.onclick(function (e) {
            //         e = e || window.event
            //         if (e.detail === 0) {
            //           done()
            //         }
            //       })
            //     } else {
            //       done()
            //     }
            //   }
            // })
            that.resultAssemble = '当前为TCO工单'
          } else {
            var data = Array.from(new Set(res))
            var hasdata = []
            for (let i = 0; i < data.length; i++) {
              hasdata.push(data[i])
            }
            that.hasAssemble = hasdata
            console.log(that.hasAssemble.length)
            console.log(that.needAssemble.length)
            if (that.hasAssemble.length !== that.needAssemble.length) {
              that.work_Test()
            } else {
              that.dialogVisible = true
              // that.$message({
              //   type: 'success',
              //   message: '检验通过！'
              // })
              that.resultAssemble = '通过'
            }
          }
        } else {
          // that.$message({
          //   type: 'error',
          //   message: '此工单未装配！'
          // })
          that.dialogVisible = true
          that.resultAssemble = '未装配'
        }
      }).catch(function (err) {
        if (err.response.status === 401) {
          alert('用户未登录或登录已过期')
          localStorage.clear()
          that.$router.push({path: '/user/login/'})
          window.location.reload()
        } else if (err.response.status === 403) {
          that.$message({
            type: 'error',
            message: '权限不够！'
          })
        } else if (err.response.status === 404) {
          that.$message({
            type: 'error',
            message: '未找到数据！'
          })
        } else if (err.response.status === 504) {
          that.$message({
            type: 'error',
            message: '服务器未响应！'
          })
        } else {
          that.$message({
            type: 'error',
            message: '查询失败！'
          })
        }
      })
    },
    work_Test () {
      if (this.hasAssemble.length !== 0 && this.needAssemble.length !== 0) {
        var data = JSON.parse(JSON.stringify(this.needAssemble))
        for (let i = 0; i < this.hasAssemble.length; i++) {
          for (let j = 0; j < data.length; j++) {
            if (data[j] === this.hasAssemble[i]) {
              data.splice(j, 1)
              j--
            }
          }
        }
        console.log(data)
        if (data.length !== 0) {
          this.NoAssemble = data
          // this.$message({
          //   type: 'error',
          //   message: '校验未通过！'
          // })
          this.dialogVisible = true
          this.resultAssemble = '未通过'
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped >
body {
  text-align:center
}
.PregroupTest >>> .el-dialog__header span{
  color:red!important;
  font-size: 20px!important;
  font-weight: bold!important;
}
</style>



