<template>
  <div id="Navigation">
    <el-menu
      :default-active="activeIndex"
      :router="true"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item  index="group_pre_assembly_input_system"><router-link tag="li" :to="{path:'/group_pre_assembly/group_pre_assembly_input_system'}">预组组装录入系统</router-link></el-menu-item>
      <el-menu-item  index="pregroup_test"><router-link tag="li" :to="{path:'/test/pregroup_test'}">预组装配检验</router-link></el-menu-item>
      <el-menu-item  index="compound_glue"><router-link tag="li" :to="{path:'/glue/compound_glue'}">配胶</router-link></el-menu-item>
      <!-- <el-menu-item  index="web_serial_api"><router-link tag="li" :to="{path:'/api/web_serial_api'}">api</router-link></el-menu-item> -->
      <el-menu-item  index="osa_equipment_information_entry"><router-link tag="li" :to="{path:'/equipment/osa_equipment_information_entry'}">OSA设备信息录入</router-link></el-menu-item>
      <el-menu-item  index="material_glue"><router-link tag="li" :to="{path:'/material/material_glue'}">test</router-link></el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  data () {
    return {
      activeIndex: '1',
      logged: false,
      backstage: false,
      username: '',
      timer: ''
      // proscenium: true
    }
  },
  mounted () {
    this.proscenium = true
  },
  methods: {
    handleSelect (key, keyPath) {
      this.activeIndex = key
    }
  }
}
</script>

<style>
  #Navigation {
    margin-bottom: 20px;
  }
  .atooltip {
  background:rgba(255, 255, 255, 0.5) !important;
  font-size: 1px !important;
  max-height: 10px !important;
  max-width:200px !important;
  }
  /* .atooltip{
    background:red !important
  } */
</style>



